import React from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { ButtonActionsPropType, ButtonActionsStateType } from '@interfaces/buttons/type.buttons-action';


export type ButtonActionsGoBackPropTypeCombined = ButtonActionsPropType & RouteComponentProps;

const ButtonActionsGoBackMainPropDefault: ButtonActionsPropType = {
  onClick: ( callback: any) => void(0),
  isActive: false,
  className: '',
} 

class ButtonActionsGoBackMain extends React.Component<ButtonActionsGoBackPropTypeCombined, ButtonActionsStateType> {
  constructor(props: any){
    super(props);
    this.state = {
      isActive: props.isActive,
    }
  }
  static defaultProps = ButtonActionsGoBackMainPropDefault;

  onClick = (event: any ) => {
    this.setState({
      isActive: !this.state.isActive
    },() => {
      if(this.props.history){
        if( this.props.history.length > 1 ){
          this.props.history.goBack();
        }else{
          this.props.history.push('/worlds');
        }
        if(this.props.onClick){
          this.props.onClick(event);
        }
      }
    });
  }
  
  render(){
    const { props } = this;
    return (
      <div className={`${props.className} ${this.state.isActive?'anim_zoom_out':'anim_zoom_in'}`} onClick={this.onClick}>
        { 
          props.render && 
            props.render({}) 
        }
      </div>
    )
  }
}


export const ButtonActionsGoBack =  withRouter( ButtonActionsGoBackMain );
