import React from 'react';
import { ButtonActionsPropType, ButtonActionsStateType } from '@interfaces/buttons/type.buttons-action';

export interface ButtonSmallWrapperPropType {
  onClick: ( event: any ) => void,
  render?: ( callback: object ) => JSX.Element,
  isActive: boolean,
  className: string,
}

export interface ButtonSmallWrapperStateType{
  isActive: boolean,
}

export const ButtonSmallWrapperPropDefault: ButtonSmallWrapperPropType= {
  onClick: () => void(0),
  isActive: false,
  className: '',
} 

export class ButtonSmallWrapper extends React.Component<ButtonSmallWrapperPropType,ButtonSmallWrapperStateType > {
  constructor(props: any){
    super(props);
  }
  public static defaultProps = ButtonSmallWrapperPropDefault;

  render(){
    const { props } = this;
    return (
      <div className={`button_icons_small ${props.className}`}>
        {props.children}
      </div>
    )
  }
}
