import React from 'react';
import { LazyFoxModalWindow } from '@module-modal-window/module-modal-window';
import { DataArray } from '@interfaces/global-type/global-type'
import { CallbackRender } from '@interfaces/global-type/global-type';
import { OwnIcon } from '@icons/module-icons';

export interface LazyFoxSelectBoxPropsType{
  onSelect: ( callback: object ) => void,
  className: string,
  data: DataArray<any>,
  render?: ( callback: CallbackRender ) => JSX.Element,
  isError: boolean,
  placeholder: string,
  selectedData: Array<any>,
  style: object,
  limit: number,
  useMultipleSelect: boolean,
  targetKeys: string,
  title: string,
  useModal: boolean,
  errorText: string,
}

export interface LazyFoxSelectBoxStateType{
  isOpen: boolean,
  selectedData: Array<any>,
  data: DataArray<any> ,
}

export const LazyFoxSelectBoxPropsDefault: LazyFoxSelectBoxPropsType  ={
  onSelect: ( callback: any ) => void(0),
  className: '',
  data: [],
  isError: false,
  placeholder: 'Select the options...',
  selectedData: [],
  style: {},
  limit: 0,
  useMultipleSelect: false,
  targetKeys: '',
  title: 'SELECT THE DROP VALUE',
  useModal: true,
  errorText: `You can't leave this field unselected!`
}


const getValueOfObject = ( object: any, keys: string): object | any => {
  if( typeof object === 'object'){
    for (var i in object){
      if( i === keys ){
        return object[i].toString();
        break;
      }else{
        if( typeof object[i] === 'object'){
          return getValueOfObject(object[i], keys);
          break;
        }else{
          return object[i].toString();
          break;
        }
      }
    }
  }else{
    return object;
  }
}

const dataDestructor = (data: DataArray<any>, selectedData: Array<any>): Array<any> => {
  return data.map( ( items: any, index: number ) => {
    let isSelected = false;
    for( let i = 0, arrLength = selectedData.length; i < arrLength; i++ ){
      if( items === selectedData[i] ){
        isSelected = true;
      }
    }
    return {
      value: items,
      isSelected,
    }
  })
}

export class LazyFoxSelectBox extends React.Component<LazyFoxSelectBoxPropsType, LazyFoxSelectBoxStateType> {
  constructor( props: any ){
    super(props);
    this.state = {
      isOpen: false,
      selectedData: props.selectedData,
      data: dataDestructor( props.data, props.selectedData ),
    }
  }

  public static defaultProps = LazyFoxSelectBoxPropsDefault;
  private mainModalWindow = React.createRef<LazyFoxModalWindow>();
  
  onClick = (event: any) => {
    this.mainModalWindow.current?.show();
  }

  onSelectItems = ( items: any, index: number ) => {
    let data: DataArray<any> = this.state.data;

    if( this.props.useMultipleSelect ){
      if(this.state.selectedData.length < this.props.limit && this.props.limit > 0){
        data[index].isSelected = !items.isSelected;
      }else{
        if(this.props.limit > 0){
          data[index].isSelected = false;
        }else{
          data[index].isSelected = !items.isSelected;
        }
      }
    }else{
      data = data.map( ( testItems: any, testIndex: number ) => {
        testItems.isSelected = testIndex === index ?true:false;
        return testItems;
      })
    }
    let selectedData: Array<any> = [];
    for( let i: number = 0, arrLength = this.state.data.length; i < arrLength; i++ ){
      if( this.state.data[i].isSelected){
        selectedData.push( this.state.data[i].value );
      }
    }
    this.setState({ selectedData, data }, 
      () => this.props.onSelect( { selectedData, selectedItem: items.value, selectedIndex: index } )
    );
  }

  componentDidUpdate = (prevProps: any, prevState: any) => {
    if( prevProps.data !== this.props.data ){
      this.setState({
        selectedData: this.props.selectedData,
        data: dataDestructor( this.props.data, this.props.selectedData ),
      })
    }
  }

  render(){
    const { props, state } = this;
    return (
      <div className = {"lazyfox_select_box"}>
        { props.useModal &&
          <div className = {`lazyfox_select_box_value ${props.isError ? 'error_box' : ''}`} onClick = { this.onClick }>
            <label className = {"value_title"}>{ props.title }</label>
            <div className = {"value_box"}>
              { state.selectedData.length > 0 &&
                state.selectedData.map( (items, index) => (
                  <label className = {"value_text"} key={index}>{ getValueOfObject(items, props.targetKeys) }</label>
                ))
              }
              { state.selectedData.length < 1 &&
                <label className = {"value_text_placeholder"}>{ props.placeholder }</label>
              }
            </div>
          </div>
        }

        { /*  if not using modal poppuop  */}
        { props.useModal === false && 
          <div className = {"lazyfox_select_box_list d_flex flex_wrap"}>
            {
              state.data.map(( items: any, index: number) => {
                return (
                  <div key = { index } className = {`select_box_items ${items.isSelected?'selected_items':''}`} onClick = { () => this.onSelectItems( items,index ) } style = { props.style }>
                    { props.render && props.render({ items, index, isSelected: items.isSelected }) }
                    { !props.render && getValueOfObject(items, props.targetKeys) }
                  </div>
                )
              })
            }
          </div>
        }
        { /*  end if not using modal poppuop  */}

        { props.isError &&
          <div className={"error_wrappers"}>
            <OwnIcon.IconAlert className={"icons_error"} />
            <label className = {"error_text"}>{ props.errorText }</label>
          </div>
        }

        { props.useModal &&
          <LazyFoxModalWindow  ref = { this.mainModalWindow } headerTitle = { props.title }>
            <div className = {"lazyfox_select_box_list d_flex flex_wrap"}>
              {
                state.data.map(( items: any, index: number ) => {
                  return (
                    <div key={`select_input_${index}`}  className = {`select_box_items ${items.isSelected?'selected_items':''}`} onClick = { () => this.onSelectItems( items,index ) } style = { props.style }>
                      { props.render && props.render({ items: items.value, index, isSelected: items.isSelected }) }
                      { !props.render && getValueOfObject(items.value, props.targetKeys) }
                    </div>
                  )
                })
              }
            </div>
          </LazyFoxModalWindow>
        }
      </div>
    )
  }
}
