import React from 'react';
import { Link } from 'react-router-dom';

export interface ButtonIconsSecondaryPropType{
  onClick: (event: object) => void,
  placeholder: string,
  children: any,
  isActive: boolean,
  isDark: boolean,
  isUseAlert: boolean,
  alertValue: number,
  isDesktop: boolean,
  to: string,
  className: string,
  leftPlaceholder: string,
  rightPlaceholder: string,
  useLink: boolean,
}

export const ButtonIconsSecondaryPropDefault : ButtonIconsSecondaryPropType = {
  placeholder: '',
  onClick: () => null,
  children: null,
  isActive: false,
  isDark: false,
  isUseAlert: false,
  alertValue: 0,
  isDesktop: false,
  className: '',
  to: '/',
  leftPlaceholder: '',
  rightPlaceholder: '',
  useLink: false,
}

export class ButtonIconsSecondary extends React.Component<ButtonIconsSecondaryPropType> {
  constructor( props: any ){
    super(props);
  }
  public static defaultProps = ButtonIconsSecondaryPropDefault;

  onClick = ( event: any ) => {
    this.props.onClick({ event });
  }
  calculateAlertValue = ( value: number ) : string | number => {
    if (value > 10){
      return '10+';
    }else{
      return value;
    }
  }
  render(){
    const { props } = this;
    if( !props.useLink ){
      return (
        <div className = { props.isDesktop?'desktop-items secondary_button_icons':'secondary_button_icons' } >
          <div className={`${ props.isDark ?'button_icons_dark':'button_icons'} ${props.className}`} onClick={this.onClick}>
            <div className={`${props.isActive?"button_icons_box active":"button_icons_box"} d_flex align_items_center`} >
              { ( props.leftPlaceholder !== '') &&
                <label className={"text"} >{props.leftPlaceholder}</label>
              }
              <div className={"button_icons_bandle"}>
                { props.children }
              </div>
              { ( props.rightPlaceholder !== '' ) &&
                <label className={"text"} >{props.rightPlaceholder}</label>
              }
            </div>
          </div>
        </div>
      )
    }else{
      return (
        <Link to = { props.to } className = { props.isDesktop?'desktop-items secondary_button_icons':'secondary_button_icons' } >
          <div className={`${ props.isDark ?'button_icons_dark':'button_icons'} ${props.className}`} onClick={this.onClick}>
            <div className={`${props.isActive?"button_icons_box active":"button_icons_box"} d_flex align_items_center`} >
              { ( props.leftPlaceholder !== '') &&
                <label className={"text"} >{props.leftPlaceholder}</label>
              }
              <div className={"button_icons_bandle"}>
                { props.children }
              </div>
              { ( props.rightPlaceholder !== '' ) &&
                <label className={"text"} >{props.rightPlaceholder}</label>
              }
            </div>
          </div>
        </Link>
      )
    }
  }
}
