import React from 'react';
import { ButtonActionsPropType, ButtonActionsStateType } from '@interfaces/buttons/type.buttons-action';

export const ButtonActionsLikePropDefault: ButtonActionsPropType = {
  onClick: () => void(0),
  isActive: false,
  className: '',
  totalLike: 1,
} 

export class ButtonActionsLike extends React.Component<ButtonActionsPropType, ButtonActionsStateType>{
  constructor(props: any){
    super(props);
    this.state = {
      isActive: props.isActive,
      totalLike: props.totalLike
    }
  }
  public static defaultProps = ButtonActionsLikePropDefault;

  calculateTotalLike = ( isActive: boolean ) => {
    if(this.state.totalLike){
      if( isActive ){
        return this.state.totalLike + 1 ;
      }else{
        if( this.state.totalLike > 0 ) {
          return this.state.totalLike - 1 ;
        }
        return 0
      }
    }
     
  }
  onClick = ( event: any ) => {
    this.setState({
      isActive: !this.state.isActive,
      totalLike: this.calculateTotalLike(!this.state.isActive)
    },() => {
      // do call API
    });
  }
  render(){
    const { props } = this;
    return (
      <div className={`${props.className} ${this.state.isActive?'anim_zoom_out':'anim_zoom_in'}`} onClick={this.onClick}>
        { 
          props.render &&
            props.render({
              isActive: this.state.isActive,
              totalLike: this.state.totalLike?this.state.totalLike:0
            }) 
        }
      </div>
    )
  }
}
