import React, { Component } from "react";
import { OwnImage } from "@img/module-images";
import { ApiBody, BASE_URL, ServerRequest } from "@utils/utils-server-request";
import { ContentLoader } from "@module-layout/loader/ContentLoader";



export class MyApps extends Component<any,  any> {
  static defaultProps = {
    api: 'apps'
  };
  constructor(props: any){
    super(props)
    this.state = {
      isFetching: false,
      apps: [],
      appsData: {},
    }
  }

  componentDidMount(): void {
    this.getData();
  }

  getData = () => {
    const config: ApiBody  = {
      endpoint: `api/${this.props.api}.json`,
    }
    if(!this.state.isFetching){
      this.setState({isFetching: true})
      new ServerRequest(BASE_URL).get( config, ({data, isFound, isError, isFetching}) =>{
        if(isFound){
          this.setState({ isFound, isError, isFetching, apps: data})
          this.getDataDetails(data, 0)
        }else if(isError){
          // this.setState({ isFound, isError, isFetching})
        }
      })
    }
  }

  getDataDetails = (arr: Array<any>, index: number) => {
    if(arr.length < 1) return null;
    const item = arr[index];
    if(this.state.appsData[item] && index <=  arr.length - 1){
      this.getDataDetails(arr, index+1)
      return null
    }
    const config: ApiBody  = {
      endpoint: `api/app_${item}.json`,
    }
    new ServerRequest(BASE_URL).get( config, ({data, isFound, isError, isFetching}) =>{
      if(isFound){
        const details = this.state.appsData;
        details[item] = data;
        this.setState({ appsData: details})
        if(index <=  arr.length - 1){
          this.getDataDetails(arr, index+1)
        }
      }else if(isError){
        if(index <=  arr.length - 1){
          this.getDataDetails(arr, index+1)
        }
      }
    })
  }


  render(){
    const { props, state } = this;
    return(
      <>
        {state.isFetching &&
          [1,2,3,4,5,6].map((item: any, i: number) => (
            <div className="box oneleft " style={{padding: '5px'}} key={i}>
              <ContentLoader arrLength = {1}  isShow = {true} height = {"50%"} />
            </div>
          ))
        }
        { state.apps.map((item: any, i: number) => {
          const appItem = state.appsData[item];
          if(!appItem){
            return (
              <div className="box m3 oneleft childflex" style={{padding: '8px'}} key={item}>
                <div className=" shadow childflex onebg-white">
                  <ContentLoader arrLength = {1}  isShow = {true} height = {"50%"} />
                </div>
              </div>
            )
          }
          return (
            <div className="box m3 oneleft childflex" style={{padding: '8px'}} key={item}>
              <div className=" shadow childflex onebg-white">
                <a href={`/ridwan/store/view/?id=${appItem.id}`}>
                  <div className="img">
                    <div className=" imglist">
                      <img src={OwnImage.SecretImgLong} />
                      <div className="backimg after3 imgrender onebg-semiungu" style={{backgroundImage:  `url(${appItem.image?.[0]?.src})`}} />
                      {/* <div className="release">'.strtoupper($data[0][$i]['apps_status']).'</div> */}
                    </div>
                  </div>
                  <div style={{padding: '10px'}}>
                    <div className="subtittle onec-blue">{appItem.tech}</div>
                    <div className="tittle">{appItem.title}</div>
                  </div>
                </a>
              </div>
            </div>
          )
        })}
      </>
    )
  }
}