import React from 'react';
import ReactDOM from 'react-dom';

import '@assets/sass/main.scss'
import { MainApps, RouterPropType } from 'index-module';
import ScreenHome from '@screens/screen-home';
import ScreenPortofolio from '@screens/screen-portofolio';
import screenApps from '@screens/screen-apps';
import { CreateData } from '@data-generator/generateData';
import screenView from '@screens/screen-view';

const dataRouteMap: RouterPropType = [
  {
    component: ScreenHome,
    exact: true,
    path: '/'
  },
  {
    component: ScreenPortofolio,
    exact: true,
    path: '/ridwan'
  },
  {
    component: screenApps,
    exact: true,
    path: '/ridwan/store'
  },
  {
    component: screenView,
    exact: true,
    path: '/ridwan/store/view'
  },
  
  
  // {
  //   component: screenAdd,
  //   exact: true,
  //   path: '/add'
  // },
  // {
  //   component: screenAdd,
  //   exact: true,
  //   path: '/edit/:contentId/:indexId'
  // },
  // {
  //   component: screenChart,
  //   exact: true,
  //   path: '/keranjang'
  // },
  // {
  //   component: screenReport,
  //   exact: true,
  //   path: '/laporan'
  // },
  // {
  //   component: screenInfo,
  //   exact: true,
  //   path: '/annisee'

  // },
  {
    component: ScreenHome,
    exact: true,
    path: '/*'
  }
];

declare global {
  interface Window { _dataCreator: any; }
}
window._dataCreator =  CreateData;
class Apps extends React.Component{
  render(){
    return(
      <MainApps renderFrom={"browser"} dataRouteMap={dataRouteMap} />
    )
  }
}

export default Apps;
