import React from 'react';
import { Css } from '@interfaces/css-style/type.css-style';
import { SileEfectPropType, RenderHeaderPropType, LazyFoxModalWindowPropType, LazyFoxModalWindowStateType } from '@interfaces/modal-window/type.modal-window'; 


const SLIDE_EFFECT : SileEfectPropType= {
  left:{
    marginLeft:0,
  },
  right:{
    marginRight:0,
  },
  bottom:{
    marginBottom:0,
  }
}
const SHOW_MODAL: Css = { 
  display:'block',
  opacity:1,
}
const HIDE_MODAL: Css = { 
  display:'block',
  opacity:0,
}

const RenderHeader = ( props: RenderHeaderPropType ) => {
  if( props.useHeader ){
    if( !props.renderHeader  ){
      return(
        <div className={'modal_header'} style = { props.headerStyle }>
          <label>{ props.headerTitle }</label>
        </div>
      )
    }else{
      return props.renderHeader( props.dismiss ? props.dismiss : () => null  );
    }
  }else{
    return null
  }
}

export const LazyFoxModalWindowPropDefault: LazyFoxModalWindowPropType ={
  showOnStart: false,
  position: 'left',
  useCloseOnClickBackground: true,
}

export class LazyFoxModalWindow extends React.Component<LazyFoxModalWindowPropType, LazyFoxModalWindowStateType > {
  constructor( props: LazyFoxModalWindowPropType ){
    super(props);
    this.state = {
      isOpen: false,
      style_main_window_open:{},
      style_modal_body: {}
    }
  }
  private static defaultProps = LazyFoxModalWindowPropDefault;
  private isMounteds: boolean = false;

  componentDidMount = () => {
    this.isMounteds = true;
    if(this.props.showOnStart){
      this.show();
    }
  }
  componentWillUnmount = () => {
    this.isMounteds = false;
  }

  public show = () => {
    document.body.classList.add('lazy_fox_modal_window_is_open');
    setTimeout(()=>{
      if(this.isMounteds){
        this.setState({
          isOpen: true,
          style_main_window_open: SHOW_MODAL,
        },() => {
          setTimeout(()=>{
            if(this.isMounteds){
              this.setState({
                style_modal_body: SLIDE_EFFECT[this.props.position?this.props.position:'left']
              })
            }
          },100)
        });
      }
    },20);
  }

  public dismiss = ( ) =>{
    this.setState({
      style_main_window_open: HIDE_MODAL,
      style_modal_body:{}
    },()=>{
      this.setModalToClosed();
    });
  }

  useCloseOnClickBackground = () => {
    if(this.props.useCloseOnClickBackground){
      this.dismiss();
    }
  }

  setModalToClosed = () => {
    setTimeout(() => {
      if(this.isMounteds){
        this.setState({
          style_main_window_open:{},
          isOpen:false,
        });
        document.body.classList.remove('lazy_fox_modal_window_is_open');
        if(this.props.onDismiss){
          this.props.onDismiss(true)
        }
      }
    },300);
  }
  render(){
    const { props, state } = this;
    if(state.isOpen){
      return(
        <div className={`lazy_fox_modal_window ${props.className}`} style={state.style_main_window_open}>
            <div className={"close_box_area"} onClick={this.useCloseOnClickBackground}></div>
            <div className={`modal_content ${props.position}`} style={{...props.style,...state.style_modal_body}}>
              <RenderHeader useHeader = { props.useHeader } renderHeader = { props.renderHeader } dismiss = { this.dismiss } headerTitle = { props.headerTitle } headerStyle = { props.headerStyle }/>
              <div className={'modal_body'} style = { props.bodyStyle }>
                { props.children }
              </div>
            </div>
        </div>
      )
    }else{
      return null
    }
  }
}
