import React from 'react';
import { RegexManager } from '@utils/utils-regex-manager';
import { DataArray } from '@interfaces/global-type/global-type'
import { CallbackRender } from '@interfaces/global-type/global-type';
import { OwnIcon } from '@icons/module-icons';
import { ButtonActionsCollection } from '@module-button/module-buttons';

export interface LazyFoxInputBoxPropsType{
  onChange: ( callback: any ) => void,
  onBlur?: ( callback: any ) => void,
  onFocus?: ( callback: any ) => void,
  onSubmit?: ( callback: any ) => void,
  className?: string,
  render?: ( callback: CallbackRender ) => JSX.Element,
  isError: boolean,
  placeholder: string,
  style?: object,
  title: string,
  errorText?: string,
  isMultiline?: boolean,
  rows?: number,
  maxLength?: number | undefined,
  minLength?: number ,
  type: string, 
  value: any,
  idKey: any,
  targetError?: any
}

export interface LazyFoxInputBoxStateType{
  isError: boolean,
  value?: any,
  type?: string,
  isShowPassword?: boolean,
}

export const LazyFoxInputBoxPropsDefault: LazyFoxInputBoxPropsType  ={
  onChange: ( callback: any ) => void(0),
  onBlur: ( callback: any ) => void(0),
  onFocus: ( callback: any ) => void(0),
  onSubmit: ( callback: any ) => void(0),
  className: '',
  isError: false,
  placeholder: 'Please type something...',
  style: {},
  idKey: 'textEditor',
  isMultiline: false,
  title: 'HERE EXAMPLE TITLE',
  errorText: `You can't leave this field empty!`,
  maxLength: undefined,
  minLength: 0,
  rows: 3,
  value: '',
  type: "null"  // username | youtube | password | null | repassword | email | name | none,
}

const INPUT_TYPE_ENUM: any = {
  username: 'email',
  youtube: 'text',
  password: 'password',
  null: 'text',
  repassword: 'password',
  number: 'number',
  email: 'email',
  name: 'text',
  none: 'text'
}


export class LazyFoxInputBox extends React.Component<LazyFoxInputBoxPropsType, LazyFoxInputBoxStateType> {
  constructor( props: any ){
    super(props);
    this.state = {
      value: props.value,
      isError: props.isError,
      type: INPUT_TYPE_ENUM[props.type],
    }
  }
  private RegTest: any = new RegexManager();
  public static defaultProps = LazyFoxInputBoxPropsDefault;

  componentDidUpdate = (prevProps: any) => {
    const { props } = this;
    if(prevProps.value !== props.value){
      this.setState({value: props.value })
    }
    if(prevProps !== props){
      this.setState({ isError: props.isError})
    }
  }

  onChange = (event: any) => {
    const { props } = this;
    const isError: boolean = this.RegTest[ props.type ]( event.target.value , props.minLength);
    const value: any =  event.target.value;
    this.setState({ isError, value }, () => {
      this.props.onChange({isError, value, idKey: props.idKey});
    })
  }

  onClickEyeBtn = (event: any) => {
    const { state } = this;
    this.setState({type: state.type === 'text' ? 'password' : 'text'})
  }

  render(){
    const { props, state } = this;
    return (
      <div className = {"lazyfox_select_box"} >
        <div className = {`lazyfox_input_box_value ${state.isError ? 'error_box' : ''}`} >
          <label className = {`value_title ${state.isError ? 'error_text': ''}`}>{ props.title }</label>
          <div className = {'value_box'} style = {( props.type === 'password' || props.type === 'repassword') ? {paddingRight: '45px'} : {}}>
            { !props.isMultiline &&
              <input maxLength = { props.maxLength}  
                minLength = { props.minLength } 
                className = {"input_box_values"}
                onChange = { this.onChange } 
                onFocus = { props.onFocus }
                onBlur = { props.onBlur }
                value = {  state.value }
                type = {state.type}
                placeholder = { props.placeholder }/>
            }
            { props.isMultiline &&
              <textarea  maxLength = { props.maxLength}  
                minLength = { props.minLength } 
                className = {"input_box_values"} 
                placeholder = { props.placeholder } 
                onChange = { this.onChange } 
                onFocus = { props.onFocus }
                onBlur = { props.onBlur }
                value = { state.value }
                rows = { props.rows }>{ state.value }</textarea>
            }
            {( props.type === 'password' || props.type === 'repassword') &&
              <ButtonActionsCollection onClick = {this.onClickEyeBtn} className={"btn_open_pasword"} render = {() => (
                <>
                  <OwnIcon.IconEyeOpen isShow = {state.type === 'password'} className={"btn_open_pasword_icon"} />
                  <OwnIcon.IconEyeClosed isShow = {state.type === 'text'} className={"btn_open_pasword_icon"} />
                </>
              )}/>
            }
          </div>
        </div>
        { state.isError &&
          <div className={"error_wrappers"}>
            <OwnIcon.IconAlert className={"icons_error"} />
            <label className = {"error_text"}>{ props.errorText }</label>
          </div>
        }
      </div>
    )
  }
}
