import React from 'react';
import { ButtonActionsPropType, ButtonActionsStateType } from '@interfaces/buttons/type.buttons-action';

export const ButtonActionsCollectionPropDefault: ButtonActionsPropType = {
  onClick: ( event: any) => void(0),
  //render: ( callback: object ) => (<></>),
  isActive: false,
  className: '',
}

export class ButtonActionsCollection extends React.Component<ButtonActionsPropType, ButtonActionsStateType>{
  constructor(props: any){
    super(props);
    this.state = {
      isActive: props.isActive
    }
  }
  public static defaultProps = ButtonActionsCollectionPropDefault;

  onClick = ( event: any ) => {
    this.setState({
      isActive: !this.state.isActive
    },() => {
      // do call API
      if(this.props.onClick){
        this.props.onClick( event );
      }
    });
  }
  render(){
    const { props } = this;
    return (
      <div className={`${props.className} ${this.state.isActive?'anim_zoom_out':'anim_zoom_in'}`} onClick={this.onClick}>
        { 
          props.render &&
            props.render({
              isActive: this.state.isActive
            }) 
        }
      </div>
    )
  }
}
