import React from 'react';
import { DataArray, CallbackRender } from "@interfaces/global-type/global-type";

interface FlatListPropType {
  onScroll?: ( callback: object ) => void,
  render?: ( callback: CallbackRender ) => JSX.Element,
  data: DataArray<any>,
}

interface FlatListStateType{
  isActive?: boolean,
}

const FlatListPropDefault : FlatListPropType= {
  // render: ( callback: object ) => ,
  onScroll: ( callback: object ) => void(0),
  data: [],
} 

class FlatList extends React.Component<FlatListPropType, FlatListStateType>{
  constructor( props: FlatListPropType ){
    super( props );
  }
  public static defaultProps = FlatListPropDefault;

  componentDidMount=()=> {
    window.addEventListener('scroll', this.onScroll);
  }

  componentWillUnmount=()=> {
    window.removeEventListener('scroll', this.onScroll);
  }

  onScroll=( e: any )=>{
    let node = e.target.scrollingElement;
    const isBottom = node.scrollHeight - node.scrollTop <= node.clientHeight;
    if(this.props.onScroll) this.props.onScroll({ isBottom });
  }
  render(){
    const {props} = this;
    return props.data.map(( items: any, index: any, arr: any ) => (
      props.render &&
        props.render({ items, index, arr })
    ))
  }
}

export default FlatList;
